import helper from '@/common/js/helper.js';
import util from '@/common/js/util.js';
import mInput from '@/components/m-input.vue';
export default {
	components: {
		mInput
	},
	data() {
		return {
			mobile: '',
			pwd: '',
		};
	},
	onLoad() {
		if (!helper.APP_DATA.session) {
			return
		}
		helper.APP_DATA.session = helper.getSession()
		if (helper.APP_DATA.session) {
			uni.reLaunch({
				url: '/pages/tabbar/tabbar-1/tabbar-1'
			})
			return
		}
	},
	methods: {
		bindLogin() {
			if (!util.isMobile(this.mobile)) {
				uni.showToast({
					icon: 'none',
					title: '手机号码为 11 个数字'
				});
				return;
			}
			if (this.pwd.length < 6 || this.pwd.length > 12) {
				uni.showToast({
					icon: 'none',
					title: '密码为 6~12 个字符'
				});
				return;
			}

			const data = {
				mobile: this.mobile,
				pwd: this.pwd,
			}
			helper.ajax.post('/v1/vis/login', data, res => {
				if (res.status === 200) {
					helper.setSession(res.data,ret=>{
						uni.showToast({
							title: '登录成功'
						});
						setTimeout(() => {
							uni.reLaunch({
								url: '/pages/tabbar/tabbar-1/tabbar-1'
							})
						}, 1500)
					})
					return
				}
				uni.showToast({
					title:res.message,
					icon:'none',
				})
			})
		}
	}
}
